function show_hb() {
  jQuery(".hb").fadeIn(400);
}

function hide_hb() {
  jQuery(".hb").fadeOut(400);
}

function in_hb() {
  jQuery("#suggest_box").fadeIn(200);
}

function out_hb() {
  jQuery("#suggest_box").fadeOut(200);
}

function set_suggestion(text) {
  jQuery("#suggestion_text").replaceWith(
    "<div id=" + '"suggestion_text">' + text + "</div>",
  );
}

jQuery("#hb_suggest").hover(in_hb, out_hb);
